import "./App.css";
import * as React from "react";
import Storyfinal from "./comp/Storyfinal";
import {
  RouterProvider,
  createHashRouter,
  Navigate,
  createBrowserRouter,
} from "react-router-dom";
import Rootlayout from "./comp/Rootlayout";
import Hometest from "./comp/Hometest";
import ReactGA from "react-ga";
import Error from "./comp/Error";
import Testwp from "./comp/Testwp";

const TRACKING_ID = "UA-237706751-1";
ReactGA.initialize(TRACKING_ID);

const routerx = createBrowserRouter(
  [
    {
      path: "/",
      element: <Rootlayout />,
      errorElement: <Error />,
      children: [
        { index: true, element: <Hometest /> },
        { path: "/test", element: <Testwp /> },
        { path: "*", element: <Navigate to={<Error />} /> },
      ],
    },
  ],
 
);

function App() {
  return (
    <>
      <RouterProvider router={routerx} />
    </>
  );
}

export default App;
