import React, { useEffect } from "react";
import { useRef, useState } from "react";

import Typed from "typed.js";

function Mainheader() {
  const el = React.useRef(null);
  const typed = React.useRef(null);

  React.useEffect(() => {
    const options = {
      strings: [
        "enjoy tattra <i>website</i> ",
        "best <strong>website</strong> ever",
        // "HTML characters &times; &copy;",
      ],
      typeSpeed: 50,
      backSpeed: 50,
      loop: true,
    };

    typed.current = new Typed(el.current, options);

    return () => {
      typed.current.destroy();
    };
  }, []);


  return (
    <>
      <header className=" position-relative">
        <style
          dangerouslySetInnerHTML={{
            __html:
              "\n    /* Default height for small devices */\n    #intro-example {\n      height: 400px;\n    }\n\n    /* Height for devices larger than 992px */\n    @media (min-width: 992px) {\n      #intro-example {\n        height: 600px;\n      }\n    }\n  ",
          }}
        />
        <div
          id="intro-example"
          className="px-5 text-center image-style  "
          style={{
            backgroundImage:
              'url("https://insanelygoodrecipes.com/wp-content/uploads/2021/03/White-Chocolate-Oreo-Cookie-Balls-768x1152.webp")',
          }}
        >
          <div className="d-flex justify-content-center align-items-center h-100">
            <div className="text-white ">
              <h1 className="mb-3">
                <div className="type-wrap text-success h1  ">
                  <span style={{ whiteSpace: "pre" }} ref={el} />
                </div>
              </h1>
              <a
                className="btn btn-warning shadow btn-lg m-2"
                href="https://cmure.com/tattranew"
                role="button"
                rel="nofollow"
                target="_blank"
              >
                watch more <i className=" fab  fa-youtube"></i>
              </a>
              <a
                className="btn btn-warning shadow btn-lg m-2"
                href="https://cmure.com/tattranew"
                target="_blank"
                role="button"
              >
                see more atrtical here <i className=" fas fa-pepper-hot"></i>
              </a>
            </div>
          </div>
        </div>
      </header>
    </>
  );
}

export default Mainheader;
