import { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Apicontext } from "./Apicontext";
import React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { Oval } from "react-loader-spinner";
import Mainheader from "./Mainheader";

const Hometest = () => {
  let { allPostes } = useContext(Apicontext);

  return (
    <>
      {allPostes.length > 0 ? (
        <div className="">
        <h1 className="text-3xl  font-bold underline">
      Hello world!
    </h1>
          <div className=" row">
            <div className=" col-12">
              <Mainheader />
            </div>
            <div className=" col-12 my-5 ">
              <div className=" container">
                <div className=" row">
                  {allPostes.map((item, index) => (
                    <Link
                      className="col-lg-4"
                      key={index}
                      to={`/stories/${item.id}`}
                    >
                      <Card className=" my-3 shadow">
                        <CardMedia
                          component="img"
                          alt="green iguana"
                          height="200"
                          image={item.media}
                        />
                        <CardContent className="">
                          <Typography gutterBottom variant="h5" component="div">
                            {item.title.split(" ").slice(0, 5).join(" ")}
                          </Typography>
                          <Typography gutterBottom variant="h5" component="div">
                            {item.category_id.split(" ").slice(0, 5).join(" ")}
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            {item.content.split(" ").slice(0, 5).join(" ")}
                          </Typography>
                        </CardContent>
                        <CardActions>
                          <Button size="small">Share</Button>
                          <Button size="small">Learn More</Button>
                        </CardActions>
                      </Card>
                    </Link>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <div className=" position-absolute top-0 bottom-0 start-0 end-0 bg-light d-flex justify-content-center align-items-center">
          <Oval
            height={80}
            width={80}
            color="#4fa94d"
            wrapperStyle={{}}
            wrapperClass=""
            visible={true}
            ariaLabel="oval-loading"
            secondaryColor="#4fa94d"
            strokeWidth={2}
            strokeWidthSecondary={2}
          />
        </div>
      )}
      
    </>
  );
};

export default Hometest;
